<template>
    <aside>
        <div class="calculator__wrapper">
            <div class="calculator">
                <h2 class="calculator__title">ПОСЧИТАЙТЕ ПРИБЫЛЬ</h2>
                <div class="calculator__subtitle">
                    ДОБРИВО ЭФФЕКТИВНЕЕ ХИМИИ
                </div>
                <hr class="calculator__separator">
                <form action="#">
                    <label>
                        Выберите культуру <br>
                        <select @change="calculate" name="culture" v-model="selectedOption">
                            <option value="3100">Пшеница ~ 3100 грн/т</option>
                            <option value="2100">Капуста ~ 2100 грн/т</option>
                            <option value="4000">Кукуруза ~ 4000 грн/т</option>
                        </select>
                    </label> <br>
                    <label>
                        Урожайность ц/га <br>
                        <input min="0" @input="calculate" type="number" v-model="yieldAmount">
                    </label> <br>
                    <label>
                        Стоимость хим. удобрения, грн/га <br>
                        <input min="0" @input="calculate" type="number" v-model="price">
                    </label> <br>
                    <label>
                        Посевная площадь, га <br>
                        <input min="0" @input="calculate" type="number" v-model="area">
                    </label>
                </form>
                <hr class="calculator__separator">
                <p v-if="warningMessage" class="calculator__warningMessage">{{ warningMessage }}</p>
                <p>Прибыль от добрива</p>
                <p class="calculator__profit">{{ profit }}</p>
                <p>грн</p>
            </div>
        </div>
    </aside>
</template>

<script>
    export default {
        name: "Calculator",
        data: function () {
            return {
                selectedOption: 3100,
                yieldAmount: 10,
                price: 5000,
                area: 15,
                profit: 8125,
                warningMessage: "",
            }
        },
        methods: {
            calculate: function () {
                if (!this.selectedOption ||
                    !this.yieldAmount ||
                    !this.price ||
                    !this.area) {
                    this.warningMessage = "Все поля должны быть заполнены и иметь положительное значение";
                } else this.warningMessage = "";
                this.profit = parseInt(this.selectedOption) + this.yieldAmount + this.price + this.area;
            }
        }
    }
</script>

<style scoped>
    .calculator__wrapper {
        background-color: #e9e5e4;
        padding: 28px 0;
    }
    .calculator {
        padding-top: 5px;
        border: 2px dashed #a7a9ac;
        width: 610px;
        margin: 0 28px;
    }
    .calculator__title {
        font-size: 75px;
    }
    .calculator__subtitle {
        font-size: 29px;
    }
    .calculator label {
        font-size: 29px;
    }
    .calculator label {
        width: calc(100% - 90px);
    }
    .calculator input,
    .calculator select {
        width: 100%;
        font-size: 29px;
        height: 60px;
        padding: 0 15px;
        border: 1px solid #d1d3d4;
        color: #a7a9ac;
    }
    .calculator hr {
        width: 60%;
    }
    .calculator__profit {
        font-size: 50px;
        color: palevioletred;
    }
    .calculator__warningMessage {
        color: crimson;
    }
    @media (max-width: 2599px) {
        .calculator__wrapper {
            padding: 14px 0;
        }
        .calculator {
            margin: 0 14px;
            width: 430px;
        }
        .calculator__title {
            font-size: 38px;
        }
        .calculator__subtitle {
            font-size: 20px;
        }
        .calculator input,
        .calculator select,
        .calculator label {
            font-size: 20px;
        }
        .calculator input,
        .calculator select {
            height: 46px;
        }
        .calculator option {
            font-size: 12px;
            width: 100px;
        }
    }
    @media (max-width: 800px) {
        .calculator {
            width: 370px;
        }
        .calculator label {
            width: calc(100% - 50px);
        }
        .calculator__title{
            font-size: 32px;
        }
        .calculator__subtitle {
            font-size: 17px;
        }
    }
</style>